import { useEffect, useMemo, useState } from "react";
import { TransactionAll } from "../api/transactions";

export default function useColumns() {
  const columns = useMemo(
    () => [
      {
        Header: "#",
        accessor: "id"
      },
      {
        Header: "",
        accessor: "name"
      },
      {
        Header: " ",
        accessor: "count"
      },
      {
        Header: "  ",
        accessor: "price"
      },
      {
        Header: "",
        accessor: "product_id"
      }
    ],
    []
  );

  return columns;
}
