import React, { useEffect, useRef, useState } from 'react'
import { Form } from 'react-bootstrap'
import { categoryAll } from '../api/category'
import { productCreate } from '../api/product'

const AddProduct = () => {
    const [action, setAction] = useState("")
    const [article, setArticle] = useState("")
    const [category, setCategory] = useState("")
    const [compound, setCompound] = useState("")
    const [contraindications, setContraindications] = useState("")
    const [count, setCount] = useState(0)
    const [description, setDescription] = useState("")
    const [modeOfApp, setModeOfApp] = useState("")
    const [name, setName] = useState("")
    const [price, setPrice] = useState(0)
    const [weight, setWeight] = useState("")
    const [file, setFile] = useState([])
    console.log(file);
    const form = useRef()
    const postProductCreate = async (e) => {
        e.preventDefault()
        const {data} = await productCreate(form.current)
        console.log(data)
        if (data.message=="ok"){
            setAction("")
            setArticle("")
            setCompound("")
            setContraindications("")
            setCount(0)
            setDescription("")
            setModeOfApp("")
            setName("")
            setPrice(0)
            setWeight("")
            setFile(null)
        }
    }
    


    const [categoryMassive,setCategoryMassive]=useState([])
    const getCategory = async () => {
        const { data } = await categoryAll()
        setCategoryMassive(data?.all_category)
    }
    useEffect(() => {
        getCategory()
    }, [])
    console.log(categoryMassive)
    return (
        <div className="content-wrapper">
            {/* Content Header (Page header) */}
            <div className="content-header">
                <div className="container-fluid">
                    <div className="row mb-2">
                        <div className="col-sm-6">
                            <h1 class="m-0">  (  )</h1>
                        </div>{/* /.col */}
                        <div className="col-sm-6">

                        </div>{/* /.col */}
                    </div>{/* /.row */}
                </div>{/* /.container-fluid */}
            </div>
            <section className="content">
                <div className="container-fluid">
                    <div className="row">
                        {/* left column */}
                        <div className="col-12 ">
                            {/* general form elements */}
                            <div className="card card-primary card-outline card-tabs">
                                <div class="card-header p-0 pt-1 border-bottom-0">
                                    <ul class="nav nav-tabs" id="custom-tabs-one-tab" role="tablist">
                                        <li class="nav-item">
                                            <a class="nav-link active" id="custom-tabs-one-home-tab" data-toggle="pill" href="#custom-tabs-one-home" role="tab" aria-controls="custom-tabs-one-home" aria-selected="true"> </a>
                                        </li>

                                    </ul>
                                </div>
                                {/* /.card-header */}
                                {/* form start */}
                                <form ref={form} onSubmit={postProductCreate}>
                                    <div className="card-body">
                                        <div className="form-group">
                                            <label htmlFor="action"></label>
                                            <textarea
                                                onChange={(e) => setAction(e.target.value)}
                                                type="text"
                                                className="form-control"
                                                id="exampleInputEmail1"
                                                placeholder='  '
                                                name='action' 
                                                value={action}/>
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="article"></label>
                                            <input
                                                onChange={(e) => setArticle(e.target.value)}
                                                type="text"
                                                className="form-control"
                                                id="exampleInputPassword1"
                                                placeholder='   '
                                                name='article' 
                                                value={article}/>
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="category"></label>
                                            <Form.Select
                                                className="form-control"
                                                onChange={e => setCategory(e.target.value)}
                                                name="category"
                                            >
                                                {
                                                    categoryMassive?.map(item=>
                                                        <option value={item?.name}>{String(item?.name)}</option>)
                                                }
                                                
                                               

                                            </Form.Select>
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="compound"></label>
                                            <textarea
                                                onChange={(e) => setCompound(e.target.value)}
                                                type="text"
                                                className="form-control"
                                                id="exampleInputPassword1"
                                                placeholder='  '
                                                name='compound'
                                                value={compound} />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="contraindications"></label>
                                            <textarea
                                                onChange={(e) => setContraindications(e.target.value)}
                                                type="text"
                                                className="form-control"
                                  
                                                placeholder='  '
                                                name='contraindications'
                                                value={contraindications}  />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="count"></label>
                                            <input
                                                onChange={(e) => setCount(e.target.value)}
                                                type="number"
                                                className="form-control"
                                                id="exampleInputPassword1"
                                                placeholder=' '
                                                name='count'
                                                value={count} />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="description"></label>
                                            <textarea
                                                onChange={(e) => setDescription(e.target.value)}
                                                type="text"
                                                className="form-control"
                                                id="exampleInputPassword1"
                                                placeholder=' '
                                                name='description'
                                                value={description} />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="lang"></label>
                                            <Form.Select
                                                className="form-control"
                                                name='lang'
                                            >
                                                <option value={"ru"}>  </option>
                                                <option value={"en"}> </option>

                                            </Form.Select>
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="modeOfApp"> </label>
                                            <textarea
                                                onChange={(e) => setModeOfApp(e.target.value)}
                                                type="text"
                                                className="form-control"
                                                id="exampleInputPassword1"
                                                placeholder='   '
                                                name='modeOfApp'
                                                value={modeOfApp} />
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="name"></label>
                                            <input
                                                onChange={(e) => setName(e.target.value)}
                                                type="text"
                                                className="form-control"
                                                id="exampleInputPassword1"
                                                placeholder='  '
                                                name='name'
                                                value={name}  />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="price"></label>
                                            <input
                                                onChange={(e) => setPrice(e.target.value)}
                                                type="number"
                                                className="form-control"
                                                id="exampleInputPassword1"
                                                placeholder=''
                                                name='price'
                                                value={price} />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="weight"> </label>
                                            <input
                                                onChange={(e) => setWeight(e.target.value)}
                                                type="text"
                                                className="form-control"
                                                id="exampleInputPassword1"
                                                placeholder=' '
                                                name='weight'
                                                value={weight} />
                                        </div>
                                        
                                        <div className="form-group">
                                            <label htmlFor="file"></label>
                                            <input
                                                type="file"
                                                multiple
                                                onChange={(e) => setFile(e.target.files)}
                                                className="form-control"
                                                id="exampleInputPassword1"
                                                
                                                name='file' />
                                        </div>
                                    </div>
                                    {/* /.card-body */}
                                    <div className="card-footer">
                                        <button
                                            type='submit'
                                            className="btn btn-primary"
                                        //  onClick={(e)=>postProductCreate(e)}
                                        ></button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

        </div>
    )
}

export default AddProduct